import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './core/user-management/components/login/login.component';
import { RegisterComponent } from './core/user-management/components/register/register.component';
import { ListingComponent } from './features/listing-calculations/components/listing/listing.component';


const routes: Routes = [
  {path:"", component: LoginComponent },
  {path:"Login", component:LoginComponent},
  {path:"Register", component:RegisterComponent},
  {path:"Listing", component:ListingComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
