import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

import { Router } from '@angular/router';
import { LoginModel } from '../models/login.model';
import { RegisterModel } from '../models/register.model';


@Injectable({
  providedIn: 'root'
})
export class UserManagementService {

  constructor(private http: HttpClient, private router:Router) { }
  GetUsers()
  {
    return this.http.get(environment.apiUrl + "Users/GetUsers");
  }
  // GetUserById(Id: number)x
  // {
  //   return this.http.get(environment.apiUrl + "Users/?id=" + Id);
  // }
  EditUser(body: RegisterModel)
  {
    return this.http.put(environment.apiUrl + "Users/PutUsers" + body.Id, body)
  }
  Register(body: any)
  {
    return this.http.post(environment.apiUrl + "Users/PostUsers", body);
    //return this.http.get(environment.apiUrl + "weatherforecast");
  }
  Login(body:LoginModel)
  {
    return this.http.get(environment.apiUrl + "Users/Login?UserName=" + body.UserName+"&Password="+ body.Password);
  }
  
  isLoggedIn()
  {
    if(localStorage.getItem("username"))
    {
      return true;
    }
    else
    {
      return false;
    }
    
  }
  LogOut()
    {
      localStorage.clear();
      this.router.navigate(["Login"]);
    }
  
}
