import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ListingCalculationsService {

  constructor(private http: HttpClient) { }

 
  GetFullList()
  {
    return this.http.get(environment.apiUrl + "ListOfCalculations/GetListOfCalculations")
  }
  AddData(body: any)
  {
    return this.http.post(environment.apiUrl + "ListOfCalculations/PostListOfCalculations", body);
   
  }
  DeleteRow(Id: number)
  {
    return this.http.delete(environment.apiUrl + "ListOfCalculations/DeleteListOfCalculations/" + Id);
  }
}
