import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';



import { Subscription } from 'rxjs';
import { UserManagementService } from '../../services/user-management.service';
import { LoginModel } from '../../models/login.model';



@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit, OnDestroy {
   
  constructor(private router: Router, private httpService : UserManagementService) { }
  form : LoginModel = {
    UserName: '',
    Password: '',
  }
  Response: any;
  Login = new  Subscription();
  ngOnInit() {
  }
  ngOnDestroy()
  {
   this.Login.unsubscribe();
  }
 
  onLogin()
  {
    let Login = this.httpService.Login(this.form).subscribe(
      res => {
        this.Response = res;
        if(this.Response)
        {
          alert("LoggedSuccessfully");
          localStorage.setItem("username", this.Response.UserName);
          localStorage.setItem("userId", this.Response.Id);
          this.router.navigate(["Listing"]);
        }
        else
        {
          alert("Wrong UserName Or Password");
        }
        
      },
      err => {
        alert("Error In Login");
      }
    )
  }
  GoToRegister()
  {
    
    this.router.navigate(['Register']);
  }

}
